.flex-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 1.5rem 3rem;
}

.left-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.track-crypto-heading,
.real-time-heading {
  font-size: 5.5rem;
  margin: 0;
}

.track-crypto-heading {
  color: var(--white);
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: var(--white);
}

.track-crypto-heading:hover {
  color: var(--black);
  -webkit-text-stroke-color: var(--white);
  transition: all 0.3s;
}

.real-time-heading {
  color: var(--blue);
}

.info-text {
  color: var(--grey);
  font-size: 1rem;
}

.btn-flex {
  display: flex;
  gap: 1.5rem;
  margin-top: 1.5rem;
  justify-content: flex-start;
}

.phone-container {
  width: 50%;
  position: relative;
  padding-top:2rem;   
}

.iphone {
  position: absolute;
  z-index: 100;
  min-width: 300px;
  right: 0;
  width: 50%;
}

.gradient {
  position: absolute;
  min-width: 250px;
  right: 0rem;
  width: 40%;
  top: 7rem;
}

@media only screen and (max-width: 1320px) {
  .track-crypto-heading,
  .real-time-heading {
    font-size: 5rem;
  }
}

@media only screen and (max-width: 1000px) {
  .iphone {
    min-width: 250px;
  }

  .gradient {
    min-width: 200px;
    top: 6rem;
  }
}

@media only screen and (max-width: 800px) {
  .flex-box {
    flex-direction: column;
  }

  .left-container {
    width: 100%;
    align-items: center;
  }
  .phone-container {
    display: block;
    margin: auto;
    margin-top: 2rem;
  }

  .iphone {
    left: 5% !important;
  }

  .gradient {
    left: 15% !important;
  }

  .track-crypto-heading,.real-time-heading {
    font-size: 3rem;
    text-align: center;
  }
  .track-crypto-heading {
    -webkit-text-stroke-width: 1px;
  }

  .btn-flex {
    justify-content: center;
  }

  .info-text {
    text-align: center;
  }
}
